const getMockResponse = (configs, options = null) => {

  options = assign({
    nested: false,
  }, options)

  const data = {}

  each(configs, (config, key) => {
    data[key] = config.default
  })

  if (options.nested) {
    return convertToNested(data)
  }

  return data
}

class SourceError extends Error {

  constructor(source, error) {
    super(`${error.message} ("${source}" source)`)
    this.name = `SourceError`
  }
}

export { getMockResponse, parseSource, SourceError }
